body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
    display: flex;
    width: auto;
    max-width: 85%;
    justify-content: flex-start;
    align-items: center;
    margin: 0 auto;
  }
  header > .logo {
    flex: 10 1;
    text-align: left;
    padding: 1%;
  }
  header >.singIn {
    min-width: 350px;
    font-weight: 700;
    font-size: 1.5em;
    position: relative;
    cursor: pointer;
  }
  header >.singIn > p{
    padding-bottom: 5%;
  }
  .singIn > .loggedIn::before{
    content: '';
    position: absolute;
    width: 100%;
    height: 5px;
    top: 100%;
    left: 0;
    background: #1D1FDD;
  }
  .firebaseui-card-content > form{
    margin-bottom: 0%;
    margin-top: 0%;
  }
  button.firebaseui-idp-github{
    background-color: #1D1FDD!important;
  }
  
  @media (max-width: 980px) {
    header {
      max-width: 95%;
      padding-top: 5%;
    }
    header >.singIn{
      font-size: 1.2em;
    }
    span.firebaseui-idp-text.firebaseui-idp-text-long {
      display: none;
    }
    header >.singIn {
      min-width: 120px;
    }
    header >.singIn > p{
      text-align: center;
    }
    .firebaseui-idp-github{
      text-align: center!important;
    }
    .firebaseui-idp-icon-wrapper{
      display: inline-block!important;
    }

  }
form {
    width: 65%;
    margin: 0 auto;
    margin-bottom: 15%;
    margin-top: 5%;
  }
  .filter-wrap{
    width: 100%;
    margin: 0 auto;
    position: relative;
  }
  .filter-wrap > label {
    position: absolute;
    z-index: 9;
    left: 5%;
    top: 28%;
    color: #1a02e1;
  }
  input.form-filter {
    outline: none;
    display: block;
    position: relative;
    width: 100%;
    border: none;
    border-radius: 30px;
    line-height: 45px;
    padding-left: 11%;
    background-color: #F5F5F5;
    color: #a2a2a2;
    font-size: 1.1em;
  }
  button.search-btn{
    font-family: neuehanskendrickBold;
    margin-top: 5%;
    background-color: #1D1FDD;
    font-size: 1.1em;
    text-align: center;
    color: #FFFFFF;
    border-radius: 30px;
    line-height: 40px;
    width: auto;
    min-width: 200px;
  }
.user-card {
    display: flex;
    justify-content: flex-start;
    max-width: 95%;
    align-items: center;
    padding-bottom: 5%;
    margin-bottom: 5%;
    border-bottom: 1px solid rgb(209, 209, 209);
  }
  .user--img {
    margin-right: 5%;
    max-width: 85px;
    max-height: 85px;
    overflow: hidden;
    border-radius: 50%;
  }
  .user--img > img {
    max-width: 100%;
  }
  .user--info{
    text-align: left;
  }
  .user--info > h3{
    font-family: neuehanskendrickLight;
    font-size: 1.5em;
    text-transform: capitalize;
    letter-spacing: 0.8px;
  }
  .user--info > p{
    font-family: neuehanskendrickLight;
    font-size: 1.1em;
    color: #1D1FDD;
  }
  article {
    text-align: left;
    padding: 4% 0% 2% 1%;
    max-width: 95%;
    border-bottom: 1px solid rgb(209, 209, 209);
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 2%;
  }
  article > a{
    flex: 10 1;
    display: block;
    text-decoration: none;
    color: black;
    font-size: 1.25em;
  }
  article >p.date{
    font-size: 0.8em;
    position: absolute;
    left: 1%;
    top: 0%;
    font-family: neuehanskendrickBold;
    color: #1D1FDD;
  }
  article >p.date > span{
    color: black;
  }
  article > button {
    font-family: neuehanskendrickBold;
    font-size: 1.1em;
    text-align: right;
    display: block;
    min-width: 120px;
    color: #1D1FDD;
    cursor: pointer;
    transition: all 100ms ease-in-out;
    border-bottom: 2px solid #FFFFFF;
    background: transparent;
  }
  article > button:hover {
    color: rgb(12, 13, 99);
    border-bottom: 2px solid #1D1FDD;
  }
  @media (max-width: 980px) {
    .user-card{
      max-width: 100%;
      padding: 0% 1% 5% 1%;
    }
    .user--info > p{
      padding-left: 1%;
    }
    article {
      max-width: 100vw;
      padding: 4% 1% 2% 1%;
    }
  }
  @media (max-width: 420px) {
    article >p.date{
      display: none;
    }
  }
@font-face {
  font-family: neuehanskendrickBold;
  src: url(/static/media/neuehanskendrick-bold.b784e541.woff);
}
@font-face {
  font-family: neuehanskendrickLight;
  src: url(/static/media/neuehanskendrick-extralight.4d799288.woff);
}
.App {
  text-align: center;
  font-size: 14px;
}
p, h1, h2, h3 , h4{
  margin: 0;
}
h1{
  font-family: neuehanskendrickBold;
  font-size: 5em;
  color: #1D1FDD;
  margin-bottom: 5%;
}
p{
  font-family: neuehanskendrickLight;
  color: #1d1d1d;
  font-size: 1em;
  text-align: left;
}
button {
  border: none;
  display: block;
  margin: 0;
  padding: 1%;
  outline: none;
}

p.subtitle{
  margin-bottom: 5%;
}
.container {
  width: auto;
  height: auto;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 45%;
  max-width: unset;
}

.container--info{
  padding: 0% 3% 0% 2%; 
  min-width: 48%;
}
.container--img {
  min-width: 45%;
  align-self: flex-start;
  padding-top: 5%;  
}
.container--img > img {
  max-width: 100%;
}
@media (max-width: 980px) {
  .container {
    flex-direction: column;
  }
  .container--info{
    padding: 0;
  }
  article {
    max-width: 100vw;
  }
  p.subtitle{
    width: 98%;
    margin: 0 auto;    
  }
}
